}
.header__middle-link {
  font-size: 14px;
  color: #4f4f4f;
}
.header__middle-link:hover {
  color: #5b2069;
  text-decoration: none;
}
.header__middle-btn {
  width: 170px;
  height: 51px;
  background-color: #702283;
  border: none;
  border-radius: 5px;
  font-family: "RussoOne";
  font-size: 16px;
  letter-spacing: 1px;
  color: #fff;
  cursor: pointer;
}
.header__middle-btn span {
  display: inline-block;
  width: 36px;
  height: 25px;
  background-color: #fff;
  color: #4f4f4f;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  margin: 0 15px 0 20px;
  padding-top: 4px;
}
