.error
  &__inscription
    position: relative
    &-title
      position: relative
      padding-top: 93px
      margin-bottom: 55px
      text-align: center
      font-family: 'RussoOne'
      font-size: 320px
      line-height: 80%
      text-transform: uppercase
      background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%)
      background-clip: text
      color: transparent
      text-shadow: 0px 4px 50px rgba(0, 0, 0, 0.05)
    &-img
      position: absolute
      top: 140px
      left: 445px
  &__title
    font-family: 'RussoOne'
    font-size: 42px
    line-height: 130%
    text-align: center
    color: #333333
    margin-bottom: 16px
    text-align: center
  &__text
    margin-bottom: 24px
    font-size: 16px
    line-height: 130%
    color: #797979
    text-align: center
  &__link
    font-size: 17px
    line-height: 130%
    color: #4c4c4c
    cursor: pointer
  &__link:hover
    text-decoration: none
    color: #333
  &__btn
    text-align: center
    &-input
      width: 288px
      height: 51px
      background-color: #702283
      font-weight: 700
      font-size: 16px
      line-height: 130%
      color: #FFFFFF
      border: none
      margin-bottom: 140px
      cursor: pointer
    &-input:hover
      background-color: #5b2069
      color: #828282

@media screen and (max-width: 321px)
  .container
    width: 320px
  .error__inscription-title
    font-size: 162px
    line-height: 80%
    margin-bottom: 81px
  .error__inscription-img
    width: 138px
    height: 156px
    left: 91px
    top: 201px
  .error__title
    font-size: 22px
    margin-bottom: 8px
    &__text
      font-size: 14px
      padding: 0 16px
      margin-bottom: 24px
