@font-face {
  font-family: 'fontello';
  src: url('../fonts/fontello.eot?59227163');
  src: url('../fonts/fontello.eot?59227163#iefix') format('embedded-opentype'),
       url('../fonts/fontello.woff2?59227163') format('woff2'),
       url('../fonts/fontello.woff?59227163') format('woff'),
       url('../fonts/fontello.ttf?59227163') format('truetype'),
       url('../img/fontello.svg?59227163#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
